.post{

    height: max-content;
    width: max-content;

    margin: auto;

}


.user-info{
    border-radius: 15px 15px  0 0;

    color: black;
    background-color: white;
    min-height: max-content;
    width: 400px;
    border-color: black;
    border-style: solid;
    border-width: 1px;
    margin-left: 150px;
}

.post img{
    margin: auto;
    position: fixed;
    justify-content: left;
}

.user-message{
    border-radius: 0 0 15px 15px;
    background-color: white;
    color: black;
    padding: 5px;
    border-color: black;
    border-style: solid;
    border-width: 1px;
    margin-left: 150px;
    min-height: 75px;
    text-align: left;
}